import React from 'react';
import { Button, Modal, Form, InputGroup, Col } from 'react-bootstrap';
import "../../../../../assets/scss/plugins/forms/react-select/_react-select.scss";
import { Search } from 'react-feather';

class FiscalYearPeriodsModal extends React.Component {
    render() {
        return (
            <div>
                <Modal
                    show={this.props.show}
                    onHide={this.props.handleHide}
                    size="lg"
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            {this.props.InsertUpdate} Fiscal Year Periods
                        </Modal.Title>
                    </Modal.Header>

                    <Form onSubmit={this.props.handleSubmit} autoComplete="off">
                        <Modal.Body>
                            <Form.Row>
                                <Form.Group as={Col} md="6" controlId="Period">
                                    <Form.Label>Period</Form.Label>
                                    <Form.Control type="text"
                                        name="Period"
                                        placeholder="Enter Period" 
                                        value={this.props.Period} 
                                        onChange={this.props.changePeriod} 
                                        required 
                                    />
                                    <Form.Control.Feedback type="invalid">
                                        Please enter Period.
                                    </Form.Control.Feedback>
                                </Form.Group>
                                <Form.Group as={Col} md="6" controlId="PYear">
                                    <Form.Label>Period Year</Form.Label>
                                    <Form.Control type="text" 
                                        name="PYear"
                                        placeholder="Enter PYear" 
                                        value={this.props.PYear} 
                                        onChange={this.props.changePYear} 
                                        required
                                    />
                                    <Form.Control.Feedback type="invalid">
                                        Please enter Period Year.
                                    </Form.Control.Feedback>
                                </Form.Group>   
                            </Form.Row>
                            <Form.Row>
                                <Form.Group as={Col} md="6" controlId="PeriodOpen">
                                    <Form.Label>Period Open</Form.Label>
                                    <Form.Control type="text" 
                                        name="PeriodOpen"
                                        placeholder="Enter Period Open" 
                                        value={this.props.PeriodOpen} 
                                        onChange={this.props.changePeriodOpen} 
                                        required 
                                    />
                                    <Form.Control.Feedback type="invalid">
                                        Please enter Period Open.
                                    </Form.Control.Feedback>
                                </Form.Group>
                                <Form.Group as={Col} md="6" controlId="PeriodClose">
                                    <Form.Label>Period Close</Form.Label>
                                </Form.Group>   
                                <Form.Group as={Col} md="6" controlId="FiscalYearName">
                                    <Form.Label>Fiscal Year Name</Form.Label>
                                    <InputGroup>
                                        {/* <Form.Control value={this.props.FISCALYEARID} onChange={this.props.handleChangeFISCALYEARID} hidden /> */}
                                        <Form.Control
                                            type="text"
                                            name="FiscalYearName"
                                            value={this.props.FiscalYearName}
                                            //onChange={this.props.handleChangeFiscalYearName}
                                            placeholder="Search Fiscal Year Name"
                                            aria-describedby="inputGroupPrepend"
                                            required
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleShowSearch}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please enter Period Open.
                                        </Form.Control.Feedback>
                                    </InputGroup>
                                </Form.Group>
                            </Form.Row>

                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHide}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit" disabled={this.props.isEnabled}>
                                {this.props.InsertUpdate}
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>

                <Modal
                    show={this.props.showdelete}
                    onHide={this.props.handleHideDelete}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            Delete Fiscal Year Periods
                        </Modal.Title>
                    </Modal.Header>
                    <Form onSubmit={this.props.handleDelete} autoComplete="off">
                        <Modal.Body>
                            <Form.Group controlId="description">
                                <h4>Are you sure you want to delete this record?</h4>
                                <Form.Control type="text" placeholder="Click Delete Button" value={this.props.Period} onChange={this.props.changePeriod} readOnly />
                            </Form.Group>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHideDelete}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit">
                                Yes Delete
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>
            </div>
        );
    }
}

export default FiscalYearPeriodsModal;